.mains {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    position: absolute;
    bottom: 0px;
    left: 50%;
    width: 80%;
    transform: translate(-50%);
    flex-direction: column;
  
  .main {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 30px;
    box-shadow:0 0 10px rgba(0, 0, 0, 0.2);
    background-color: #fff;
    border-radius: 10px;
    padding: 20px;
    max-width: 400px;
    width: 80%;

  
  .input-group {
    margin-bottom: 20px;
  }
  
  .input-group label {
    display: block;
    margin-bottom: 8px;
    color: #333;
  }
  
  .input-group input {
    width: 100%;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }

  .register{
    color: blue;
    cursor: pointer;
  }
  
  .button {
    background-color: #2196F3;
    color: #fff;
    padding: 10px 15px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
  
  .button:hover {
    background-color: #1565C0;
  }
}


}
}


  