@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap'); // Add your preferred font here
@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:wght@400;700&display=swap'); // Add your preferred font here

@import "../Mixins/Mixins.scss";

// Common styles for icons
$icon-size: 25px;
$icon-margin: 8px;

.header1 {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px 30px;
    // background-color: #000000ab;
    background-color: rgba(0, 0, 0, 0.596);;
    color: #fff;
    font-family: 'Roboto', sans-serif;
    z-index: 1000;
    transition: background-color 0.3s ease;

    @include sd {
        font-size: 14px;
    }

    @include md {
        font-size: 16px;
    }

    &:hover {
        background-color: #000000ce;
    }
}

.logo-container1 {
    margin-right: 20px;

    &:hover {
        transform: scale(1.05);
    }
}

.logo1 {
    width: 80px;
    height: 50px;
    transition: transform 0.3s ease;
}

.company-name1 {
    margin-left: 50px;
    font-size: 35px;
    font-family: 'Playfair Display', serif;
    transition: margin-left 0.3s ease, font-size 0.3s ease;

    @include sd {
        display: none;
    }

    @include md {
        display: none;
    }
}

.icons-container1 {
    display: flex;
}

.login1 {
    cursor: pointer;
    font-size: 25px;
    margin-right: 30px;
    transition: color 0.3s ease;
    transition: transform 0.3s ease;


    &:hover {
        color: #FFD700;
        transform: scale(1.3);
        
    }
}

.icon1 {
    cursor: pointer;
    margin-top: $icon-margin;
    margin-right: 35px;
    transition: transform 0.3s ease;

    &:hover {
        color: #FFD700;

        transform: scale(1.3);
    }
}