@import '../Mixins/Mixins.scss';

.footer {
  // background: linear-gradient(to right, #2285ffa7, #e52e71ac);
  background: rgba(210, 210, 210, 0.329);
  color: #000000;
  padding: 40px;
  text-align: center;
  font-weight: 600;
  border-top: 0.5px solid black;

  .footer-content {
    display: flex;
    justify-content: space-around;

    @include sd {
      flex-direction: column;
    }

    .footer-section {
      flex: 1;
      max-width: 300px;

      .columnsubscribe {
        input {
          border: 2px solid rgba(131, 131, 131, 0.756);
          border-radius: 15px;
          padding: 15px;
          width: 100%;
          margin-bottom: 10px;
        }
        button {
          display: flex;
          cursor: pointer;
          padding: 15px;
          border: none;
          border-radius: 15px;
          background-color: rgba(120, 120, 120, 0.669);
          color: #000000;
          font-weight: bold;
          transition: background-color 0.3s ease;
        }
        button:hover {
          background-color: rgba(52, 52, 52, 0.849);
        }
      }

      @include sd {
        margin-bottom: 20px;
      }
    }
  }

  h4 {
    font-size: 1.5em;
    margin-bottom: 15px;
  }

  p {
    cursor: pointer;
    margin: 10px 0;
    transition: font-weight 0.3s ease;
  }

  p:hover {
    font-weight: bold;
  }
}
