@import '../Mixins/Mixins.scss';

.count-main {
  display: flex;
  // background-color: rgba(206, 206, 206, 0);
  background: rgba(210, 210, 210, 0.329);;
  padding: 40px 0;
  width: 100%;
  justify-content: space-around;

  @include sd {
    flex-direction: column;
    align-items: center;
  }

  .count {
    display: flex;
    margin-top: 0px;
    gap: 10px;
    align-items: center;
    color: rgb(0, 0, 0);

    .iconm {
      color: blue;
    }

    @include sd {
      flex-direction: column;
      text-align: center;
      margin-bottom: 20px;
    }

    span {
      font-size: 18px;
      font-weight: bold;
    }

    .CountUp {
      font-size: 24px;
      font-weight: bold;
    }
  }
}

