@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap'); // Add your preferred font here
@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:wght@400;700&display=swap'); // Add your preferred font here

@import "../Mixins/Mixins.scss";

$icon-size: 25px;
$icon-margin: 8px;

.header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px 30px;
    background-color: #ffffffbb;
    color: #000000;
    font-family: 'Roboto', sans-serif;
    z-index: 99;
    transition: background-color 0.3s ease;

    @include sd {
        font-size: 14px;
    }

    @include md {
        font-size: 16px;
    }
}

.logo-container {
    margin-right: 20px;

    &:hover {
        transform: scale(1.05);
    }
}

.logo {
    width: 80px;
    height: 50px;
    transition: transform 0.3s ease;
}

.company-name {
    margin-left: 50px;
    font-size: 35px;
    font-family: 'Playfair Display', serif;
    transition: margin-left 0.3s ease, font-size 0.3s ease;

    @include sd {
        display: none;
    }

    @include md {
        display: none;
    }
}

.icons-container {
    display: flex;
}

.login {
    cursor: pointer;
    font-size: 25px;
    margin-right: 30px;
    transition: color 0.3s ease;
    transition: transform 0.3s ease;

    .modal-content {
        position: relative;
      }
      
      .close-button {
        position: absolute;
        top: 10px;
        cursor: pointer;
        background: none;
        border: none;
        font-size: 20px;
        color: #333; /* You can customize the color */
      }
      


    &:hover {
        font-weight: 550;
        color: #000000;
        transform: scale(1.3);
        
    }
}

.icon {
    cursor: pointer;
    margin-top: $icon-margin;
    margin-right: 35px;
    transition: transform 0.3s ease;

    &:hover {
        font-weight: bold;
        color: #000000;

        transform: scale(1.3);
    }
}

