.notfound{

    height: 80vh;
    h1{
        color: red;
        font-size: 40px;
        text-align: center;
        margin-top: 170px;
    }
    p{
        color: black;
        margin-top: 70px;
        font-weight: bold;
        text-align: center;
        font-size: 30px;
    }
    button{
        background-color: blue;
        border-radius: 25px;
        padding: 20px;
        text-align: center;
        margin-top: 70px;
        margin-left: 700px;
        cursor: pointer;

        a{
            color: white;
            text-decoration: none;
            text-align: center;
        }
    }

    button:hover{
        background-color: rgb(56, 56, 56);
    }
}